a {
  text-decoration: none !important;
}

body {
    color: white;
  background-color: black;
  margin: 0px, 30px, 0px, 30px;
  font-family: IBM Plex Mono, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.nav {
  margin-right: 15px;
  list-style: none;
  display: inline-block;
}

.liststyle {
  display: inline-block;
}

.heading  {
  background: -webkit-linear-gradient(red, white);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

a:link { 
  color: pink
}

a:active {
  color: pink
}


a:visited {
  color: pink;
}


a:hover {
  color: rgb(255,100,150);
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 30s linear;
          animation: App-logo-spin infinite 30s linear;
  height: 30vmin;
}

.App-logoX {
  -webkit-animation: App-logo-spinX infinite 30s linear;
          animation: App-logo-spinX infinite 30s linear;
  height: 30vmin;
}

.App-logo1 {
  -webkit-animation: App-logo-spin infinite 30s linear;
          animation: App-logo-spin infinite 30s linear;
  height: 15vmin;
}

.App-logoX1 {
  -webkit-animation: App-logo-spinX infinite 30s linear;
          animation: App-logo-spinX infinite 30s linear;
  height: 15vmin;
}

.App-header {
  min-height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes App-logo-spinX
 {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes App-logo-spinX
 {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
